import { defineComponent, ref, onBeforeMount, defineAsyncComponent } from "vue";
import VPagination from "@hennge/vue3-pagination";
import ServiceEngagement from "@/service/ServiceEngagement/ServiceEngagement";
import moment from "moment";

export default defineComponent({
  name: "PlannedCost",
  inheritAttrs: false,
  components: {
    VPagination,
  },
  props: {
    meetingId: {
      default: "",
    },
    tabCode: {
      default: "",
    },
    meetingDate: {
      default: "",
    },
  },
  data() {
    return {
      showdelete: false,
      costList: [],
    };
  },
  setup(props, { emit }) {
    const costList = ref([]) as any;
    const list = ref([]) as any;
    const imgError = ref(1) as any;
    const activeTab = ref(1) as any;
    const tabHeading = ref(1) as any;
    const reLoadeHeading = ref(1) as any;
    const loader = ref(false) as any;
    const tabCode = ref(props.tabCode) as any;
    const currentPage = ref(1) as any;
    const totalPages = ref(1) as any;
    const meetingId = ref(props.meetingId) as any;

    //List Estimated Api
    const listEstimatedCost = async (page: any = "") => {
      loader.value = true;
      try {
        var pagination = "";
        if (currentPage.value) {
          pagination = "?page=" + currentPage.value + "&limit=" + 10 + "&status=" + 2;
        }
        var param = meetingId.value + "/" + props.tabCode + pagination;
        var res = await ServiceEngagement.engTabCostList(param);
        if (res.is_success) {
          totalPages.value = res.total_count ? res.total_count : 0;
          if (res.result && res.result.length != 0) {
            costList.value = res.result;
          }
        } else {
          costList.value = [];
        }
        emit("estimatedCost", res);
        console.log("Result", res);
      } catch (error) {
        costList.value = [];
      }
      loader.value = false;
    };


  
 

    //Tab Change Event

    const tabChange = async (tabvalue: any) => {
      console.log("tabChange", tabvalue);
      reLoadeHeading.value += 1;
      activeTab.value = tabvalue;
      if (activeTab.value == 1) {
        currentPage.value = 1;
        await listEstimatedCost();
      }
    };

    //On Before mount Function
    onBeforeMount(async () => {
      await listEstimatedCost();
    });

    return {
      costList,
      imgError,
      list,
      activeTab,
      tabHeading,
      tabChange,
      reLoadeHeading,
      loader,
      tabCode,
      currentPage,
      totalPages,
      meetingId,
      listEstimatedCost
    };
  },

  methods: {
    bindData(data: any) {
      if (data) {
        return data;
      } else {
        return "---";
      }
    },
    deletemodal(){
      this.showdelete = true;
    },
    //Alt Img Handling
    errorImg(data: any) {
      data["errorImg"] = true;
      this.imgError += 1;
    },

    //Date Formate
    dateFormat(date: any) {
      if (date == "0000-00-00" || date == '') {
        return "--";
      } else {
        return date;
      }
    },

    //Date Formate
    activitydateFormat(date: any) {
      if (date == "0000-00-00" || date == '') {
        return  moment(this.$props.meetingDate).format("YYYY-MM-DD");
      } else {
        return date;
      }
    },

    //Dollor Amount
    dollorFormat(amount: any) {
      return  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(amount)
    },

    //Edit Planned
    editForm(rowValue : any){
      this.$emit("editForm", rowValue);
    },

    download(file : any){
      if(file){
        this.$emit("openClick", file);
      }
    },
    //Delete Planned
    deletForm(rowValue : any){
      this.$emit("deleteCost", rowValue);
    },
  },
});
